.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("back.jpg");
  background-repeat: no-repeat;
  background-size: 26cm auto;
  background-position: center center;
  filter: brightness(25%);
  background-color: #3a4c7d;

}


.Nav {
  padding: .5cm 5vw 0cm 5vw;
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.NavHome {
  position: relative;
  padding: .5cm 0vw 0cm 0vw;
  color: white;
}

.NavHome::before {
  content: "";
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  /* Add this line to cover the entire height of the parent */
  left: 0;
  position: absolute;
  background-color: #3a4c7d transparent;
  filter: brightness(25%);
}


.start {
  background-color: transparent;
  border: 3px solid #00a0d9;
  background-color: #00a0d9;
  cursor: pointer;
  color: white;
  transition: .3s ease;
  margin-top: 1cm;
  font-family: acme;
  font-size: 25px;
  padding: .2cm .75cm;
  letter-spacing: .03cm;
  border-radius: 10px;

  &:hover {
    background-color: transparent;

  }
}

